* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #000000;
}

li, a {
  font-size: 22px;
  font-weight: 500;
  text-decoration: none;
  color: white;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 10%;
}

footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 10%;
  position: fixed;
  bottom: 0px;
}

.logo {
  backface-visibility: visible;
  transform-style: preserve-3d;
  animation-name: rotating;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes rotating {
  0% { transform: rotateY(0deg); }
  50% { transform: rotateY(180deg); }
  100% { transform: rotateY(360deg); }
}

.nav__links {
  list-style: none;
}

.nav__links li {
  display: inline-block;
  padding: 0px 20px;
}
.nav__links li a {
  transition: all 0.3s ease 0s;
}

.nav__links li a:hover {
  color: #edff00
}

.disabled a{
  text-decoration: line-through;
  color: rgba(255,255,255,0.4);
  cursor: not-allowed;
}




body {
  color: #E7E9EC;
}

.App {
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(300px, 700px));
  gap: 30px;
  padding: 12% 0 20px;
  justify-items: center;
  justify-content: center;
  align-items: start;

}

.intro {
  font-size: 28px;
  text-align: left;
  line-height: 50px;
  font-weight: 400; 
}

.intro a {
  font-weight: 700;
  color: white;
  font-size: 28px;
  transition: all 0.3s ease 0s;
}

.intro a:hover {
  font-weight: 700;
  color: #edff00;

}